.btn-menu {
  position: relative;
  z-index: 2;
  width: 17px;
  height: 16px;
  background-color: transparent;
  display: none;
}

@media only screen and (max-width: 1023px) {
  .btn-menu {
    vertical-align: top;
    display: inline-block;
  }
}

.btn-menu:before {
  position: absolute;
  content: '';
  top: -5px;
  right: -5px;
  bottom: -5px;
  left: -5px;
}

.btn-menu__line {
  position: absolute;
  width: 17px;
  left: 0;
  border-bottom: 2px solid #fff;
  border-radius: 2px;
  transition: transform .3s, opacity .3s;
  backface-visibility: hidden;
  will-change: transform;
}

.btn-menu__line:first-child {
  top: 0;
  transform-origin: top left;
}

.is-open-menu .btn-menu__line:first-child {
  transform: rotate(45deg) translate(1px, 1px);
}

.btn-menu__line:nth-child(2) {
  right: 0;
  left: auto;
  top: 7px;
  width: 12px;
  transform-origin: left;
}

.is-open-menu .btn-menu__line:nth-child(2) {
  opacity: 0;
  transform: scaleX(0);
}

.btn-menu__line:nth-child(3) {
  bottom: 0;
  transform-origin: bottom left;
}

.is-open-menu .btn-menu__line:nth-child(3) {
  transform: rotate(-45deg) translate(1px, -1px);
}

